.overlay {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.706);
}

.full-page-bottom-sheet {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0;
  background-color: white;
  overflow: hidden;
  transition: height 0.3s ease;
  border-top-right-radius: 16px;
  border-top-left-radius: 16px;
  z-index: 40;
}

.full-page-bottom-sheet.show {
  height: 90vh;
  transition: height 0.3s ease;
  overflow: scroll;
}

.bottom-sheet-header {
  background-color: var(--grey-50);
  padding: 16px;
  text-align: center;
}

.bottom-sheet-header2 {
  background-color: var(--white);
  padding: 16px;
  text-align: center;
}
